#App {
  background-color: beige;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#heading {
  min-width: 100%;
}

.page {
  margin: 3rem 0 3rem;
  padding: 1rem 1rem;
  min-width: 100%;
  min-height: 85vh;
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#footer {
  min-width: 100%;
  flex-shrink: 0;
}

.grid-row {
  min-width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.grid-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.half-box {
  width: 50%;
  padding: 1rem;
  box-sizing: border-box;
}

.center-items {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.large-text-box {
  margin: 1rem;
  border-radius: 1rem;
  /* background-color: white; */
}

.paper-back {
  margin: 1rem;
  border-radius: 1rem;
  background-color: white;
  padding: 1rem;
}