#result {
    font-weight: 800;
    font-size: xx-large;
    font-family: monospace;
}

#calculator-input {
    min-width: 12rem;

    display: grid;
    grid-template-rows: repeat(5, 1fr);
    grid-auto-flow: column;
    gap: 0.25rem;
}

#calculator-input > .MuiButton-root {
    font-weight: 800;
    font-size: xx-large;
    font-family: monospace;
}

#calculator-input > .two-tall {
    grid-row-start: span 2;
}
#calculator-input > .three-tall {
    grid-row-start: span 3;
}

#notes {
    min-width: 40rem;
}